<template>
	<div class="bg">
		<el-card class="box-card">
			<div slot="header" class="clearfix">
				<span>注册</span>
			</div>
			<el-form :model="form" :rules="rules" ref="form" status-icon label-width="70px">
				<el-form-item label="账号" prop="username">
					<el-input type="text" v-model="form.username" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="密码" prop="password">
					<el-input type="password" v-model="form.password" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="确认密码" prop="checkPass">
					<el-input type="password" v-model="form.checkPass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="姓名" prop="fullname">
					<el-input type="text" v-model="form.fullname" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="手机号" prop="tel">
					<el-input type="text" v-model="form.tel" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item>
					<el-checkbox-group v-model="form.isAgree">
						<el-checkbox label="同意本站用户协议" name="type"></el-checkbox>
					</el-checkbox-group>
				</el-form-item>
				<el-form-item>
					<el-button :plain="true" @click="handleRegister" type="primary">注册</el-button>
				</el-form-item>
			</el-form>
			<div class="el-footer el-row--flex">
				<div>
					<router-link to="/">登录账户</router-link>
				</div>
				<div>
					<router-link to="/">忘记密码？</router-link>
				</div>
			</div>
		</el-card>
	</div>
</template>

<script>
import { Admin } from '@/api';
export default {
	data() {
		var validate_password = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('请再次输入密码'));
			} else if (value !== this.form.password) {
				callback(new Error('两次输入密码不一致!'));
			} else {
				callback();
			}
		};
		return {
			form: {
				username: '',
				password: '',
				checkPass: '',
				fullname: '',
				tel: '',
				isAgree: false,
			},
			rules: {
				username: [
					{ required: true, message: '请输入账户名称', trigger: 'blur' },
					{ min: 3, max: 30, message: '长度在 3 到 30 个字符', trigger: 'blur' }
				],
				password: [
					{ required: true, message: '请输入密码', trigger: 'blur' },
					{ pattern: /\d{3,}/, message: '密码至少3个数字!', trigger: 'blur' }
				],
				checkPass: [
					{ validator: validate_password, trigger: 'blur' }
				],
				fullname: [
					{ required: true, message: '请输入姓名', trigger: 'blur' },
					{ min: 2, max: 40, message: '长度在 2 到 40 个字符', trigger: 'blur' }
				],
				tel: [
					{ required: true, message: '请输入手机号码', trigger: 'blur' },
					{ pattern: /^1((3|5|8){1}\d{1}|70)\d{8}$/, message: '请正确输入手机号码!', trigger: 'blur' }
				],
				isAgree: [{ required: true, message: '请同意本站协议', trigger: 'change' }],
			}
		}
	},
	created() {
	},
	methods: {
		handleRegister() {
			// 表单验证
			this.$refs.form.validate(async (valid) => {
				if (valid) {
					// 发送Ajax
					// {...this.form} 浅拷贝 this.form
					// await 配合 async 使用，async放在await最近的函数上。
					let { status, msg } = await Admin.check(this.form);

					if (status) {
						let { status, msg } = await Admin.register({ ...this.form });
						if (status) {
							this.$message.success(msg);
							// 跳转页面 -- 组件 -- 路由跳转
							this.$router.push('/');
						}
					} else {
						// 使用后台返回的信息 msg -- 信息可能不同
						this.$message.error(msg);
					}
				} else {

				}
			})
		}
	}
}
</script>

<style lang="less" scoped>
.bg {
	height: 100vh;
	background: url('./img/register-bg.jpg') center no-repeat;
	background-size: cover;

	.box-card {
		position: fixed;
		right: 50px;
		top: 50%;
		transform: translateY(-50%);
		width: 450px;

		.el-footer {
			justify-content: space-between;

			a {
				text-decoration: none;
				color: grey;
			}
		}
	}
}
</style>
